
.friendList {
padding: 0;
margin-top: 0;
margin-left: 0;
margin-right: 300px;
list-style:none;

font-family: 'Lato', sans-serif;
font-size: 40px;
font-weight: 700;
}

.item{
width: 500px;
padding: 50px;
display: flex;
align-items: center;
justify-content: left;
margin-bottom: 20px;

background-color: white;
border-radius: 20px;
box-shadow: 0 1px 4px rgba(0, 0, 0, .3),
            -23px 0 20px -23px rgba(0, 0, 0, .6),
            23px 0 20px -23px rgba(0, 0, 0, .6),
            inset 0 0 40px rgba(0, 0, 0, .1);

}


span{
display: block;
height: 20px;
width: 20px;
margin-right: 15px;
border-radius: 50%;

}

span.true{
background-color: green;
}

span.false{
background-color: red;
}

.avatar {
display: block;
margin-right: 15px;
height: 100%;
}

.name {
display: block;
margin:0;

}

